    if (this.type === "ÊôÆÊãâÂ§öÂ∑°Ê¥ãËà∞") {
      rentMoneyByDay = 800;
    } else if (this.type === "ÂáØÁæéÁëûÊóóËà∞Áâà") {
      rentMoneyByDay = 400;
    } else if (this.type === "Â®ÅÈ©∞Êô∫Ë°åÁâà") {
      rentMoneyByDay = 200;
    }
