class Customer {
  // ‰∏ãÈù¢Ë¢´Ê≥®ÈáäÊéâÁöÑÂÜôÊ≥ïÔºåÁõ∏ÂØπ‰∫é‰∏ãÈù¢rentÁöÑÂÜôÊ≥ïÔºå‰ªñÁöÑÈÄöÁî®ÊÄßÂ∑ÆÂæàÂ§öÔºåÂÖ∑‰ΩìË°®Áé∞Ôºö
  /**
   * 1.rentVechileÂ∏¶Êúâ‰∏öÂä°Ëâ≤ÂΩ©ÔºåCustomerÂèØ‰ª•Áßü‰ªª‰Ωï‰∏úË•øÔºåÊâÄ‰ª•ÊñπÊ≥ïÂêçÊîπÊàêrent
   * 2.rentVechileÊòØÁâπÂÆö‰∏öÂä°Âú∫ÊôØÁöÑÊôÆÈÄöÂáΩÊï∞ÔºårentÊé•ÂèóTÊ≥õÂûãÔºå‰ªª‰Ωï‰∏úË•øÈÉΩÂèØÁßüÔºå‰ΩÜÊòØÂèàÂøÖÈ°ªÂèóÂà∞extends objectÁöÑÁ∫¶Êùü
   * 3.rentVechileÊúÄÊó©ÂÜôÊ≥ïÔºörentVechile(myVechileArray: Array<Vechile>),‰ΩÜÊòØrentÊòØinterfaceÁöÑÊ≥õÂûãÁ∫¶Êùü List<T>
   *   ËøôÁßçÁ∫¶ÊùüÁöÑÂ•ΩÂ§ÑÔºå‰º†ËøõÊù•ÁöÑTÂèØ‰ª•Â≠òÂÖ•Âè™Ë¶ÅÊòØÂÆûÁé∞‰∫ÜListÁöÑÊï∞ÊçÆÁ±ªÂûãÈÉΩË°åÔºàÊØîÂ¶ÇArrayListÔºåLinkedListÔºâÔºåËøôÈáåÂ∞±ÊòØÂ§öÊÄÅÁöÑ‰ª∑ÂÄº
   * 4.Ê≥õÂûãÊúâ‰∏™Áº∫ÁÇπÔºåÊØîÂ¶ÇsomethingÂÜçÁºñËØëÈò∂ÊÆµÂè™Áü•ÈÅìÊòØÊ≥õÂûãÔºåÊ≤°ÂäûÊ≥ïÁü•ÈÅìÊúâcalculateRentÊñπÊ≥ïÔºåÊâÄ‰ª•Âè™ËÉΩÁî®(something as any).calculateRent();ËøáÊ∏°
   */
  // public rentVechile(myVechileArray: List<Vechile>) {
  //   let lasttotal = 0;
  //   for (let i = 0; i < myVechileArray.size(); i++) {
  //     lasttotal += myVechileArray.get(i).calculateRent();
  //   }
  //   return lasttotal;
  //   //myVechileArray.calculateRent();
  // }
  public rent<T extends object>(list: List<T>) {
    let lasttotal = 0;
    for (let i = 0; i < list.size(); i++) {
      let something = list.get(i);
      lasttotal += (something as any).calculateRent();
    }
    return lasttotal;
  }

}

