function fn3(a: string, b: string): string;
function fn3(a: number, b: number): number;
function fn3(a: number | string, b: number | string): number | string {
  if (typeof a === 'number' && typeof b == 'number') {
    return a * b
  } else {
    return a + b
  }
}
